/* aleo-300 - latin */
@font-face {
  font-family: 'Aleo';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url('../fonts/aleo-v3-latin-300.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/aleo-v3-latin-300.woff') format('woff') /* Modern Browsers */;
}
/* aleo-regular - latin */
@font-face {
  font-family: 'Aleo';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('../fonts/aleo-v3-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/aleo-v3-latin-regular.woff') format('woff') /* Modern Browsers */;
}
/* aleo-300italic - latin */
@font-face {
  font-family: 'Aleo';
  font-style: italic;
  font-weight: 300;
  font-display: swap;
  src: url('../fonts/aleo-v3-latin-300italic.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/aleo-v3-latin-300italic.woff') format('woff') /* Modern Browsers */;
}
/* aleo-700 - latin */
@font-face {
  font-family: 'Aleo';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url('../fonts/aleo-v3-latin-700.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/aleo-v3-latin-700.woff') format('woff') /* Modern Browsers */;
}
/* kaushan-script-regular - latin */
@font-face {
  font-family: 'Kaushan Script';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('../fonts/kaushan-script-v8-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/kaushan-script-v8-latin-regular.woff') format('woff') /* Modern Browsers */;
}
body {
  margin: 0;
  padding: 0;
  font-family: Aleo, "Open Sans", serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  color: #555555;
  background-color: #ffffff;
}
.container {
  margin: 0 auto;
  width: 1100px;
}
.main-nav {
  display: flex;
  justify-content: space-between;
  height: 110px;
}
.main-nav__logo-image {
  margin: 40px 0;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list {
  display: flex;
}
.site-list__item {
  padding-left: 24px;
  padding-right: 24px;
  list-style: none;
}
.site-list__item:last-child {
  padding-right: 0;
}
.site-list__link {
  padding-top: 45px;
  padding-bottom: 45px;
  height: 20px;
  font-size: 14px;
  color: #3b3b3b;
}
.site-list__link:hover {
  color: #f34949;
}
.site-list__link:active {
  color: #ebebeb;
}
.intro {
  position: relative;
  min-width: 1100px;
  height: 574px;
  background-image: url("../../img/main-photo.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
}
.intro__header {
  margin: 0;
  padding-top: 210px;
  text-align: center;
  font-family: 'Kaushan Script';
  font-size: 73px;
  line-height: 75px;
  color: #ffffff;
}
.intro__text {
  position: absolute;
  top: 320px;
  left: 50%;
  transform: translate(-50%, 0);
  color: #ffffff;
}
.intro__text::before {
  content: "";
  position: absolute;
  top: 12px;
  left: -67px;
  width: 40px;
  height: 1px;
  background-color: #ffffff;
}
.intro__text::after {
  content: "";
  position: absolute;
  top: 12px;
  right: -67px;
  width: 40px;
  height: 1px;
  background-color: #ffffff;
}
.menu__icon {
  position: relative;
  width: 155px;
  margin: 0 auto;
  margin-bottom: 76px;
}
.menu__header {
  position: absolute;
  top: 20px;
  left: 29px;
  margin: 0;
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
}
.menu__wrap-left {
  width: 520px;
}
.menu__wrap-left .menu__money {
  padding-right: 25px;
}
.menu__wrap-left .menu__money::before {
  content: "";
  position: absolute;
  top: 13px;
  right: 98px;
  width: 192px;
  height: 1px;
  background-color: #e3e1e1;
}
.menu__wrap-right {
  width: 520px;
}
.menu__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 35px;
  padding: 0;
  list-style: none;
}
.menu__item {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}
.menu__item-name {
  margin: 0;
}
.menu__money::before {
  content: "";
  position: absolute;
  top: 13px;
  right: 69px;
  width: 192px;
  height: 1px;
  background-color: #e3e1e1;
}
.menu__item-content {
  margin: 0;
  font-size: 12px;
  font-style: italic;
  font-weight: 300;
  color: #b7b7b7;
}
.menu__button-wrap {
  position: relative;
  margin: 0 auto;
  width: 133px;
}
.menu__arrow {
  position: absolute;
  top: 14px;
  right: 11px;
  fill: #b7b7b7;
}
.menu__button {
  position: relative;
  display: block;
  margin-bottom: 100px;
  padding: 0;
  padding-left: 16px;
  text-align: left;
  width: 133px;
  height: 34px;
  font-size: 14px;
  line-height: 20px;
  color: #b7b7b7;
  background-color: #ffffff;
  border: solid 1px #b7b7b7;
  text-transform: lowercase;
}
.menu__button::before {
  content: "";
  position: absolute;
  bottom: 10px;
  left: 97px;
  width: 1px;
  height: 12px;
  background-color: #b7b7b7;
}
.menu__button:hover {
  color: #555555;
  border: solid 1px #555555;
}
.menu__button:hover .menu__arrow {
  fill: #555555;
}
.menu__button:hover::before {
  background-color: #555555;
}
.gallery__header {
  position: relative;
  margin-top: 180px;
  margin-bottom: 80px;
  font-size: 22px;
  font-weight: 700;
  text-transform: uppercase;
}
.gallery__header::before {
  content: "";
  position: absolute;
  top: 11px;
  left: 177px;
  width: 200px;
  height: 1px;
  background-color: #e5e3e3;
}
.gallery__image-block {
  display: grid;
  grid-template-columns: 366px 367px 367px;
  grid-template-rows: 216px 216px;
  margin-bottom: 200px;
}
.gallery__image-link--first {
  grid-row-start: 1;
  grid-row-end: 3;
}
.page-footer {
  margin-top: 100px;
  min-width: 1100px;
  background-color: #3b3b3b;
}
.page-footer__wrap {
  display: flex;
  justify-content: space-between;
  padding-top: 100px;
  padding-bottom: 85px;
  margin: 0 auto;
  color: #ffffff;
}
.page-footer__item {
  width: 230px;
}
.navigation__name {
  margin: 0;
  margin-bottom: 7px;
}
.navigation__address {
  margin: 0;
  margin-bottom: 7px;
  font-weight: 300;
  color: #ebebeb;
}
.navigation__index {
  margin: 0;
  margin-bottom: 7px;
  font-weight: 300;
  color: #ebebeb;
}
.navigation__phone {
  margin: 0;
  margin-bottom: 7px;
}
.footer-menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.footer-menu__item {
  margin-bottom: 7px;
}
.footer-menu__link {
  color: #ffffff;
}
.footer-menu__link:hover {
  color: #f34949;
}
.footer-menu__link:active {
  color: #ebebeb;
}
.copyright__image {
  margin-bottom: 22px;
}
.copyright__owner {
  margin: 0;
  margin-bottom: 7px;
  font-weight: 300;
}
.copyright__site {
  margin: 0;
  margin-bottom: 7px;
  font-weight: 300;
}
.copyright__site-link {
  display: inline-block;
  font-weight: 400;
  color: #ffffff;
}
.copyright__site-link:hover {
  color: #f34949;
}
.copyright__site-link:active {
  color: #ebebeb;
}
.slider__header {
  position: relative;
  margin: 0;
  font-size: 20px;
  text-transform: uppercase;
}
.slider__header::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 213px;
  width: 200px;
  height: 1px;
  background-color: #e5e3e3;
}
.slider__header-wrap {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
}
.slider__switch {
  display: flex;
}
.slider__round {
  margin-left: 11px;
  width: 9px;
  height: 9px;
  background-color: #dedede;
  border-radius: 50%;
}
.slider__round--active {
  background-color: #9b9b9b;
}
.slider__round:hover {
  background-color: #9b9b9b;
}
.slider__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  margin-top: 75px;
  padding: 0;
  list-style: none;
}
.slider__desc {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
}
.slider__item-name {
  color: #555555;
}
.slider__star {
  fill: #f1df1a;
}
.slider__star--gray {
  fill: #dedede;
}
